.btn {
  display: inline-block;
  position: relative;
  padding: 25px 32px;
  font-family: "MuseoSansCyrl", sans-serif;
  text-shadow: 0.5px 0.9px 1px rgba(26, 30, 29, 0.32);
  box-shadow: 4px 8px 0 -4px #216d57;
  font-size: 18px;
  font-weight: 900;
  line-height: 1.9;
  text-decoration: none;
  text-align: center;
  color: #fff;
  border-radius: 4px;
  background-color: #34c399;
}

.block__header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5em;
}

.block__title {
  text-transform: uppercase;
  font-family: "MuseoSansCyrl", sans-serif;
  font-weight: 900;
  line-height: 1.2;
  color: #1f2023;
}

.block__title_small {
  font-size: 17px;
  line-height: 1.41;
}

.block__image-wrapper {
  position: relative;
  display: inline-block;
  flex: 0 0 123px;
  min-width: 123px;
  margin-right: 20px;
}

.block__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
}

.block .icon-decor {
  font-size: 7.7em;
  fill: #34c399;
  fill-rule: evenodd;
}

.block__descr {
  font-size: 15px;
  line-height: 1.6;
  color: #1c1e22;
}

