.title {
  font-family: "MuseoSansCyrl", sans-serif;
  font-size: 3.75em;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: normal;
}

.title-big {
  font-family: "MuseoSansCyrl", sans-serif;
  font-size: 5em;
  line-height: 1;
  text-transform: uppercase;
}

.title-big .text-row:not(:first-child) {
  margin-top: -20px;
}

.text-green {
  color: #34c399;
}

.text-blue {
