}

.text-row {
  display: block;
}

.subtitle {
  font-size: 22px;
  font-weight: 300;
  line-height: 1.27;
}

.blockquote {
  font-family: "MuseoSansCyrl", sans-serif;
  font-size: 38px;
  font-weight: 100;
  line-height: 1;
  color: #fff;
}

.blockquote__author {
  margin-top: 16px;
  font-size: 20px;
  font-weight: 700;
}

.hero {
  background-image: url(./../img/hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 0;
  color: #f3f9f0;
  height: 0;
  padding-bottom: 60.57%;
}

.hero__header {
  padding-top: 12.5em;
}

.hero__title {
  margin-bottom: 30px;
}

.hero__descr p:not(:last-child) {
  margin-bottom: 26px;
}

.feature {
  text-align: center;
}

.feature__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 40px;
  height: 40px;
  transform: rotate(45deg);
  overflow: hidden;
}

.no-touch .feature__icon:hover:before {
  transform: scaleX(2);
}

.no-touch .feature__icon:hover:after {
  transform: scaleY(2);
}

.feature__icon:before, .feature__icon:after {
  content: ' ';
  position: absolute;
  border-color: #fff;
  border-width: 1px;
  transition: transform 0.3s;
}

.feature__icon:before {
  width: 60%;
  height: 100%;
  border-top-style: solid;
  border-bottom-style: solid;
}

.feature__icon:after {
  height: 60%;
  width: 100%;
  border-left-style: solid;
  border-right-style: solid;
}

.feature__icon [class*='icon-'] {
  transform: rotate(-45deg);
}

.feature__icon .icon-check {
  color: #34c399;
  font-size: 1.125em;
}

.feature__text {
  font-family: Montserrat;
  font-size: 1.125em;
  font-weight: 300;
  line-height: 1.33;
  color: #f3f9ef;
}

.recovery {
  margin-top: -100px;
}

.recovery__descr {
  width: 75%;
}

@media only screen and (max-width: 1023px) {
  .recovery__descr {
    width: 100%;
  }
}

.recovery__subtitle {
  font-size: 30px;
  text-align: center;
}

.recovery__cta {
  text-align: center;
}

.recovery__blocks {
  margin-bottom: 4.5em;
}

.recovery__block {
  padding-top: 28px;
}

.recovery__block_card {
  padding-top: 0;
}

.card-danger {
  color: #fff;
  padding: 28px 16px 24px 28px;
  border-radius: 10px;
  box-shadow: 4.5px 7.8px 7px 0 rgba(0, 0, 0, 0.07);
  background: repeat center/50px 50px url("./../img/noise-dark.png");
}

.card-danger__title {
  font-family: "MuseoSansCyrl", sans-serif;
  font-size: 19px;
  text-transform: uppercase;
  line-height: 1.26;
  margin-bottom: 1em;
}

.card-danger__list {
  counter-reset: list;
  padding-left: 18px;
}

.card-danger__list > li:before {
  content: counter(list) ".";
  counter-increment: list;
}

.card-danger__list > li {
  position: relative;
  font-size: 13px;
  line-height: 1.62;
}

.card-danger__list > li:before {
  position: absolute;
  left: -18px;
  color: #f94e4e;
}

.about {
  margin-top: -30px;
  height: 70vw;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-image: url(./../img/about-bg.png);
  display: flex;
  align-items: center;
}

.about__content {
  width: 66.666%;
  margin-left: 33.333%;
  font-weight: 300;
  line-height: 1.75;
  color: #fff;
}

.about__content .blockquote,
.about__content > p {
  margin-bottom: 36px;
}

.about__certificates {
  display: flex;
  align-items: center;
}

.about__certificate:not(:last-child) {
  margin-right: 12px;
}
