//FONTS
@mixin fontpath($name, $name1, $file) {
  src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
  font-family: $name;
  font-style: $style;
  font-weight: $weight; }

$familyMontserrat: 'Montserrat';

@font-face {
  @include fontdefault(normal, 300, $familyMontserrat);
  @include fontpath('Montserrat Light', 'Montserrat light', Montserrat-Light); }

@font-face {
  @include fontdefault(normal, 400, $familyMontserrat);
  @include fontpath('Montserrat Regular', 'Montserrat Regular', Montserrat-Regular); }

@font-face {
  @include fontdefault(normal, 700, $familyMontserrat);
  @include fontpath('Montserrat Bold', 'Montserrat Bold', Montserrat-Bold); }

$familyMuseoSansCyr: 'MuseoSansCyrl';

@font-face {
  @include fontdefault(normal, 100, $familyMuseoSansCyr);
  @include fontpath('MuseoSansCyrl-100', 'MuseoSansCyrl-100', MuseoSansCyrl-100); }

@font-face {
  @include fontdefault(normal, 900, $familyMuseoSansCyr);
  @include fontpath('MuseoSansCyrl-900', 'MuseoSansCyrl-900', MuseoSansCyrl-900); }
